.keypad{
  display: inline-block;

  
  background-color: rgb(25, 24, 28);
  
  
}
.keyboard{
align-items: center;
justify-content: space-evenly;
  height: 3rem;
  width: 4.15rem;
  margin: 6px;
  padding: 2px;
  border: 0px;
  border-radius: 20px;
  color: rgb(0, 0, 0);
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: larger;
  background-color: rgb(120, 219, 203);
}
.keyboard:hover{
  background-color: rgb(0, 137, 195);
  
}
.mainbody{
  
  border-radius: 15px;
}
.screen{
  background-color: rgb(68, 13, 53);
  display: flex;
  height: 10rem;
  width: 19.9rem;
  border: 0px;
  font-size: 2rem;
  color:white;


}
.backspace{
  display: inline-block;
  font-size: 16px;
  margin-left: 18rem;
  border-radius: 20px;
}